@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.mainNavbar{
    position: absolute;
    width: 100vw;
    height: 70px;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 70px;
    color: #fff;
    text-transform: uppercase;
}

.mainNavbarBasket{
    position: absolute;
    width: 45px;
    height: 45px;
    right: 110px;
    transition: 1s;
}

.mainNavbarBasket img{
    cursor: pointer;
    transition: .2s;
}

.mainNavbarBasket img:hover{
    scale: 1.2;
}


.mainNavbarBasketCountBox{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40%;
    height: 40%;
    position: absolute;
    bottom: -10px;
    right: -10px;
    color: red;
    transition: .2s;
}

.mainNavbarList{
    display: flex;
    list-style: none;
    transition: 1s;
}
.mainNavbarList li{
    margin-right: 70px;
    font-weight: 500;
    cursor: pointer;
    transition: .2s;
    position: relative;
}
.mainNavbarList li:hover{
    color: #fff;
    scale: 1.1;
}
.mainNavbarList li::before{
    content: "";
    width: 0;
    height: 2px;
    position: absolute;
    background-color: #fff;
    z-index: -1;
    bottom: 0;
    transition: .2s;

}
.mainNavbarList li:hover::before{
    width: 100%;
}

.mainNavbarH1{
    text-align: center;
    width: 200px;
    height: 50px;
    position: relative;
    cursor: pointer;
}

.mainNavbarH1other{
    position: absolute;
    width: 0px;    
    transition: 1s;
    bottom: 0;
}

.mainNavbarH1:hover .mainNavbarH1other{
    background-color: red;
    width: 100%;
    height: 3px;
}

.mainNavbarH1:not(:hover) .mainNavbarH1other{
    background-color: red;
    width: 0;
    height: 3px;
}

@media only screen and (max-width: 850px){
    .mainNavbar{
    }
    
    .mainNavbar ul{
        display: none;
    }

    .mainNavbarH1 {
        position: absolute;
        right: 5%;
    }
    
}
.mainHero{
    position: absolute;
    background-color: #161616;
    width: 100vw;
    max-width: 100%;
    height: 100vh;
    transition: 1s;
    overflow: show;
}

.mainHeroCircle{
    background-color: red;
    position: absolute;
    width: 100%;
    height: 100%;
    right: 0;
    top: 0;
    -webkit-clip-path: circle(70% at right -10%);
            clip-path: circle(70% at right -10%);
    transition: 1s;
}

.mainHeroContents{
    padding-left: 6%;
    top: 25%;
    width: 40%;
    height: 60%;
    position: absolute;
}

.mainPizza{
    max-height: 50%;
    width: 50%;
    position: absolute;
    right: 5%;
    top: 20%;
    -webkit-filter:	drop-shadow(50px 100px 0px rgba(0,0,0,0.25));
            filter:	drop-shadow(50px 100px 0px rgba(0,0,0,0.25));
}

.mainHeroContent{
    width: 100%;
    color: #fff;
    margin: 20px;
}

.mainHeroContent h1{
    font-size: 5vw;
}
.mainHeroContent p{
    font-size: 1.5vw;
}

.mainHeroBtns{
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.mainHeroMakeOrder{
    height: 40px;
    width: 80%;
    border-style: none;
    color: aliceblue;
    background-color: #161616;
    border: solid #000000 2px;
    border-radius: 10px;
    box-shadow: inset 0px 0px 83px -14px rgba(0, 0, 0, 0.445);
    text-transform: uppercase;
    transition: .2s;
}


.mainHeroMakeOrder:hover{
    background-color: red;
    scale: 1.1;
}

.mainHeroFooter{
    position: absolute;
    width: 100%;
    height: 50px;
    bottom: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: #fff;
    margin-bottom: 20px;
}

.mainHeroFooter img{
    cursor: pointer;
    height: 100%;
    margin: 20px;
}

.mainHeroFooterNamesBorder{
    width: 0;
    height: 2px;
    background-color: red;
    position: absolute;
    bottom: 0;
    transition: 1s;
}

.mainHeroFooterNames{
    position: relative;
    width: 100px;
    height: 25px;
    text-align: center;
    cursor: pointer;
}

.mainHeroFooterNames:hover .mainHeroFooterNamesBorder{
    width: 100%;
}

@media only screen and (max-width: 850px){
    .mainHeroContents{
        position: absolute;
        left: 0;
        padding-top: 30%;
        width: 90%;
        height: 100%;
    }
    .mainHeroContent h1{
        font-size: 7vw;
    }
    .mainHeroContent p{
        font-size: 3vw;
    }
    .mainHeroBtns{
        max-width: 100%;
    }
    .mainHeroMakeOrder{
        border-color: red;
    }
    .mainPizza{
        transform: scaleX(-1);
        top: 10%;
        left: 20%;
    }

    .mainHeroFooter span, .mainHeroFooter div{
        display: none;
    }
    .mainHeroFooter{
        width: 100vw;
        height: 6vh;
    }
}
.mainMenu{
    position: absolute;
    width: 100vw;
    height: 100vh;
    transition: 1s;
    transform: translate(100vw,0);
}

.mainMenuCircle{
    background-color: red;
    position: absolute;
    width: 100%;
    height: 100%;
    right: 0;
    top: 0;
    -webkit-clip-path: circle(70% at left -10%);
            clip-path: circle(70% at left -10%);
    transition: 1s;
}

.mainMenuContents{
    position: relative;
    width: 100%;
    height: 100%;
    padding: 80px;
}

.mainMenuContentsItems{
    width: 100%;
    height: 100%;
    background-color: #161616;
    border: 3px solid red;
    border-radius: 20px;
    box-shadow: 0px 0px 72px 7px rgba(0,0,0,1);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
    outline: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
body{
    background-color: #161616;
    overflow: hidden;
}
